.main-services {
  padding-bottom: 80px;
}
.main-services__row-4-12 {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 40%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $textColor;
  }
}
.main-services__row__col-4-12 {
  display: grid;
  grid-template-columns: 20% 80% ;
  padding-bottom: 60px;
}

.main-services__row__col-4-12 > .fa {
  color: $shadowColor;
  text-align: center;
  font-size: 30px;
  grid-column: 1;
  grid-row: 1/3;
}

.main-services__row__col-4-12 h4,
.main-services__row__col-4-12 p {
  grid-column: 2;
}
.main-services__row__col-4-12 h4 {
  grid-row: 1;
  text-transform: uppercase;
  font-family: $headerFont;
  font-weight: bold;
  padding-bottom: 15px;
}
.main-services__row__col-4-12 p {
  font-family: $textFont;
  grid-row: 2;
  color: $textColor;
}